import React from 'react';
import './helper.css';

// Custom designed h1 tag
const CustomTypoH1 = ({ typo }) => <h1 className='typo-h1'>{typo}</h1>;

const CustomTypoH2 = ({ typo }) => <h2 className='typo-h2'>{typo}</h2>;

const CustomTypoH3 = ({ typo }) => <h3 className='typo-h3'>{typo}</h3>;

const CustomTypoH6Gray = ({ typo }) => <h6 className='typo-h6-gray'>{typo}</h6>;

const CustomTypoPara = ({ typo }) => <p className='typo-para'>{typo}</p>;

export { CustomTypoH1, CustomTypoH2, CustomTypoPara, CustomTypoH3, CustomTypoH6Gray };